@import url("https://fonts.googleapis.com/css2?family=Inter:ital,opsz@0,14..32;1,14..32&family=Kanit:wght@300&family=Roboto+Condensed:wght@700&family=Sofia+Sans&family=Ubuntu+Condensed&display=swap");
.inter {
  font-family: "Inter", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

.ubuntu-condensed {
  font-family: "Ubuntu Condensed", serif;
  font-weight: 400;
  font-style: normal;
}

.kanit {
  font-family: "Kanit", serif;
  font-weight: 300;
  font-style: normal;
}

.sofia-sans {
  font-family: "Sofia Sans", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

.roboto-condensed {
  font-family: "Roboto Condensed", serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
}

nav {
  width: 100%;
  height: 50px;
  display: flex;
  background-color: #CED4FA;
  justify-content: center;
}
nav .nav-content {
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
nav .nav-content section {
  width: 60%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
nav .nav-content section input {
  width: 240px;
  height: 30px;
  border: none;
}
nav .nav-content section input, nav .nav-content section input::placeholder {
  font-size: 20px;
}
nav .nav-content section .search-button,
nav .nav-content section .toggle {
  background-color: #000;
  color: #fff;
  height: 30px;
  font-size: 20px;
  cursor: pointer;
}
nav .nav-content section .toggle {
  display: flex;
  align-items: center;
  flex-direction: row;
  padding: 0px 11px;
}
nav .nav-content a {
  text-decoration: none;
  font-size: 30px;
  color: #000;
}

body {
  margin: 0;
  padding: 0;
}

*:focus {
  outline: none;
}

a {
  text-decoration: none;
}

main#index {
  width: 80%;
  margin-left: 10%;
}
main#index h1 {
  font-size: 60px;
  font-weight: 700;
  margin: 20px 10px 0 0;
}
main#index h2 {
  margin: 20px 0;
  font-size: 36px;
}
main#index .slider {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
main#index .slider .slider-button {
  width: 65px;
  height: 240px;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
main#index .slider .slider-button img {
  width: 65px;
  height: 65px;
}
main#index .slider .slider-button.left img {
  transform: rotate(90deg);
}
main#index .slider .slider-button.right img {
  transform: rotate(270deg);
}
main#index .slider .book-covers {
  width: 85%;
  display: flex;
  justify-content: space-between;
}
main#index .slider .book-covers img {
  width: 150px;
  height: 240px;
  cursor: pointer;
}